<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data() {
    return {
      formCustom: {
        game_review: '',
        app_review: '',
        article_review: ''
      },
    }
  },
  mounted: function() {
    const token = getToken(),
          params = {"token": token, "type":2}

    this.getData(params)
  },
  methods: {
    getData: function(params) {
      api.configEdit(params).then(res => {
        console.log(res)
        if(res.code == 0) {
          this.formCustom.game_review = res.data.game_review
          this.formCustom.app_review = res.data.app_review
          this.formCustom.article_review = res.data.article_review
        } else {
          this.$Notice.error({
            desc: res.msg
          })
        }
      }).catch(err => {
        this.$Notice.error({
          desc: err
        })
      })
    },
    handleSubmit: function() {
      const token = getToken(),
            type = 1,
            game = this.formCustom.game_review*1,
            app = this.formCustom.app_review*1,
            article = this.formCustom.article_review*1,
            params = {"token": token, "type": type, "game":game, "app" :app, "article": article};

      api.configEdit(params).then(res => {
        if(res.code == 0) {
          this.$Notice.success({
            desc: res.msg
          })
          this.$router.push({
            path: '/admin/sysConf/'
          })
        } else {
          this.$Notice.error({
            desc: res.msg
          })
        }
      }).catch(err => {
        this.$Notice.error({
          desc: err
        })
      })
    }
  }
}
</script>
<template>
<div>
  <h3><Icon type="md-build" size=22 />编辑系统配置</h3>
  <Form ref="formCustom" :model="formCustom" :label-width="90">
    <FormItem label="游戏审核">
      <RadioGroup v-model="formCustom.game_review">
        <Radio label="1">开启</Radio>
        <Radio label="2">关闭</Radio>
      </RadioGroup>
    </FormItem>
    <FormItem label="应用审核">
      <RadioGroup v-model="formCustom.app_review">
        <Radio label="1">开启</Radio>
        <Radio label="2">关闭</Radio>
      </RadioGroup>
    </FormItem>
    <FormItem label="文章审核">
      <RadioGroup v-model="formCustom.article_review">
        <Radio label="1">开启</Radio>
        <Radio label="2">关闭</Radio>
      </RadioGroup>
    </FormItem>
    <FormItem>
      <Button type="primary" @click="handleSubmit">提交</Button>
    </FormItem>
  </Form>
</div>
  
</template>
<style lang="scss" scoped>
  h3 {
    line-height: 40px;
    font-size: 18px;
    margin-bottom: 20px;
    i {
      margin: -2px 4px 0 0;
    }
  }
  .ivu-form {
		width: 28%;
	}
  .ivu-form .ivu-form-item-label {
    font-size: 14px;
  }
</style>
